html {
  scroll-behavior: smooth;
}

#container {
  height: 100vh;
  background-color: #46b7d9;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

#mainContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 40px;
  margin-top: 7%;
  flex-grow: 2;
}

#nameContainer {
  padding: 12px;
  font-size: larger;
  font-weight: 250;
  font-family: sans-serif;
  text-align: center;
  background-color: aliceblue;
}

#major {
  font-size: larger;
  font-weight: 350;
}

#portraitPhoto {
  border-radius: 50%;
  padding: 12px;
  width: 300px;
  height: auto;
}

#aboutMeContainer {
  width: 500px;
  /* margin: 0; */
}

#aboutMeText {
  margin: 0;
  /* width: 200px; */
  font-weight: 300;
}

#arrowContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 20px;
}

.arrow {
  border: solid black;
  border-radius: 3px;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 10px;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
