#projectWriteUpWrapper {
  background-image: linear-gradient(white, #eeeeee);
  height: 100%;
  display: flex;
  justify-content: center;
}

#projectDetailsContainer {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.goalContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  gap: 20px;
}

.projectImgContainer {
  display: flex;
  align-items: center;
  padding: 12px;
  width: 100%;
}

.imageWithTextRow {
  display: flex;
}

#paperTradeDashboardImg {
  width: 90%;
  margin: 0;
}
