@keyframes project {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

#projects {
  display: flex;
  align-items: center;
  justify-content: center;
}
#projectsContentContainer {
  /* padding: 0px 150px 0 150px; */
  display: flex;
  flex-direction: column;
  width: 75%;
  justify-content: center;
}

#projectsHeader {
  /* margin-top: 5%; */
  /* margin-left: 8.5%; */
  font-size: 36px;
  font-weight: 240;
  width: 80%;
}

#projectsContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  background-image: linear-gradient(white, #eeeeee);
}

#project {
  margin: 10px;
  width: 90%;
  height: auto;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 30px;

  /* animation-name: project;
    animation-duration: 4s; */
}

#projectType {
  font-weight: 230;
}

.projectTitle {
  font-weight: 300;
}

.projectDate {
  font-weight: 200;
}

#projectDetails {
  margin: 30px;
  width: 80%;
}

.projectImg {
  max-width: 85%;
}

.projectImgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.writeUpInProgressText {
  font-weight: 200;
  color: #433d3d;
}
